@import "../../assets/scss/base.scss";
.tab-product {
    padding: 12px;
    margin-bottom: 60px;
    background-color: #fff;
    .tabs {
        display: flex;
        .tab-item {
            font-size: 1.8rem;
            font-weight: 700;
            text-transform: uppercase;
            cursor: pointer;

            &.active {
                color: $color-primary;
            }

            & + .tab-item {
                margin-left: 40px;
                position: relative;

                &::before {
                    content: "";
                    position: absolute;
                    width: 15px;
                    height: 0.5px;
                    background-color: #ccc;
                    left: -30px;
                    top: 50%;
                    transform: translateY(-50%);
                    border-radius: 50%;
                }
            }

            color: #ccc;
        }
    }
    .tab-content {
        margin-top: 40px;
        .tab-pane {
            display: none;

            &.active {
                display: block;
            }
        }
    }
}

@media only screen and (max-width: 1023px) {
}

/* mobile < 740px */

@media only screen and (max-width: 739px) {
    .tabs {
        overflow-x: auto;
        overflow-y: hidden;

        padding-bottom: 24px;

        &::-webkit-scrollbar-track {
            background-color: #f5f5f5;
        }

        &::-webkit-scrollbar {
            height: 3px;
            background-color: #f5f5f5;
        }

        &::-webkit-scrollbar-thumb {
            background-color: #ff7800;
        }
    }
}

