@import "../../assets/scss/base.scss";
.home {
    background-image: url("https://vtv1.mediacdn.vn/web_images/bgvtv6all.png");
    background-size: cover;
    background-repeat: no-repeat;

    .product-slider {
        background-color: #fff;
    }
    .img__test {
        background: url("http://mauweb.monamedia.net/novahills/wp-content/uploads/2018/12/bg-introduct.png") no-repeat
            fixed;
        background-size: cover;
        width: 100%;
        height: 600px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

        .overflow {
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: rgba($color: #000000, $alpha: 0.5);
        }

        .content__left {
            position: absolute;
            top: 22%;
            left: 25%;
            width: 350px;
            height: 350px;
            border-radius: 10px;
            overflow: hidden;

            img {
                width: 100%;
                transition: all ease-out 0.4s;
            }

            &:hover img {
                transform: scale(1.1);
            }

            &.scrolled {
                animation: slide-in-left ease-out 0.4s;
                animation-duration: 1s;
            }
        }

        .content__right {
            position: absolute;
            top: 22%;
            right: 25%;
            width: 350px;
            height: 350px;
            border-radius: 10px;
            overflow: hidden;

            img {
                width: 100%;
                transition: all ease-out 0.4s;
            }

            &:hover img {
                transform: scale(1.1);
            }

            &.scrolled {
                animation: slide-in-right ease-out 0.4s;
                animation-duration: 1s;
            }
        }

        @keyframes slide-in-left {
            0% {
                transform: translateX(-100px);
                opacity: 0;
            }
            100% {
                opacity: 1;
                transform: translateX(0);
            }
        }

        @keyframes slide-in-right {
            0% {
                transform: translateX(100px);
                opacity: 0;
            }
            100% {
                opacity: 1;
                transform: translateX(0);
            }
        }
    }
}

