/* mobile < 740px */

@media only screen and (max-width: 739px) {
    .lazy-load {
        display: none;
    }

    .modal .modal__inner {
        border-radius: 0;
        overflow-y: auto;

        .product-info .product-info__image--modal {
            width: 100%;
        }
    }
}

