@import "../../assets/scss/base.scss";
.breadcrumb {
    background-color: #ededed;
    width: 100%;
    max-width: 100%;
    height: 60px;

    line-height: 60px;

    ol {
        display: flex;
        list-style: none;
        padding-left: 0;

        li {
            &.active {
                color: $color-primary;
            }
            a {
                text-decoration: none;
                color: #909090;
                font-weight: 500;
                font-size: 1.1rem;
            }
        }

        li:nth-child(1) {
            padding-right: 12px;
        }

        li + li {
            padding: 0 12px;
        }

        li + li {
            position: relative;
            &::after {
                content: "";
                position: absolute;
                width: 4px;
                height: 4px;
                background-color: #909090;
                left: 0;
                top: 50%;
                transform: translateY(-50%) rotate(45deg);
            }
        }
    }
}
